import React from "react";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import FilaDeTablaDeReporte from "./FilaDeTablaDeReporte";
import Constantes from "../Constantes/Constantes";

class VerVentas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ventas: [],
    };
  }
  async componentDidMount() {
    const respuesta = await fetch(`${Constantes.RUTA_API}/obtener_reporte.php`);
    const ventas = await respuesta.json();
    console.log(ventas);
    this.setState({
      ventas: ventas,
    });
  }
  render() {
    return (
      <div>
        <div className="column">
          <h1 className="is-size-3">
            Listado de de ventas de manera descendente
          </h1>
          <ToastContainer></ToastContainer>
        </div>
        <div className="table-container">
          <table className="table is-bordered is-striped is-narrow is-hoverable is-fullwidth">
            <thead>
              <tr>
                <th>Producto</th>
                <th>Departamento</th>
                <th>Precio</th>
                <th>Fecha de Venta</th>
              </tr>
            </thead>
            <tbody>
              {this.state.ventas.map((venta) => {
                return (
                  <FilaDeTablaDeReporte
                    key={venta.id}
                    venta={venta}
                  ></FilaDeTablaDeReporte>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default VerVentas;
